
.header {
  min-height: 10vh;
  font-size: 36px;
  color: white;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.card-front {
  background: white;
  border-top: solid white 10px;
  border-left: solid white 10px;
  margin: 10px;
  width: 240px;
  height: 336px;
  -webkit-box-shadow: 2.5px 2.5px 2.5px black;
          box-shadow: 2.5px 2.5px 2.5px black;
  vertical-align: middle;
}

.char-img {
  width: 239px;
  border-top: solid black 1px;
  border-bottom: solid black 1px;
  border-left: solid black 1px;
}

.char-title {
  color: black;
  padding: 5px;
}

.char-name {
  font-size: 18px;
}

.card-back {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(75%, white), color-stop(25%, black));
  background: -webkit-linear-gradient(white 75%, black 25%);
  background: -o-linear-gradient(white 75%, black 25%);
  background: linear-gradient(white 75%, black 25%);
  display: grid;
  grid-template-rows: 25% 50% 25%;
  margin: 10px;
  width: 336px;
  height: 240px;
  -webkit-box-shadow: 2.5px 2.5px 2.5px black;
          box-shadow: 2.5px 2.5px 2.5px black;
  vertical-align: middle;
}

.char-title {
  font-size: 24px;
}

.nickname {
  font-size: 12px;
}

.char-bio {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  font-size: 10px;  
  margin-left: 5px;
  margin-right: 5px;
  color: black;
}

.spouse-status {
  font-size: 8px;
}

table {
  table-layout: fixed;
  width: 100% ;
  border-collapse: collapse;
}

tr th {
  padding-top: 7.5px;
}
th,
td {
  vertical-align: top;
  border-spacing: 5em;
  width: 25%;
  
}

.char-quote {
  padding: 0 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  font-size: 14px;
  color: white;
}

.stark-f {
  background: #d4cdb1;
  border-top: solid #7b906f 10px;
  border-left: solid #7b906f 10px;
}

.stark-b {
  background: url("./images/stark.png") center no-repeat, -webkit-gradient(linear, left top, left bottom, color-stop(75%, #d4cdb1), color-stop(25%, #7b906f));
  background: url("./images/stark.png") center no-repeat, -webkit-linear-gradient(#d4cdb1 75%, #7b906f 25%);
  background: url("./images/stark.png") center no-repeat, -o-linear-gradient(#d4cdb1 75%, #7b906f 25%);
  background: url("./images/stark.png") center no-repeat, linear-gradient(#d4cdb1 75%, #7b906f 25%);
  background-size: contain;
  
}

.targaryen-f {
  background: #A9181F;
  border-top: solid black 10px;
  border-left: solid black 10px;
}

.targaryen-b {
  background: url("./images/targaryen.png") center no-repeat, -webkit-gradient(linear, left top, left bottom, color-stop(75%, #A9181F), color-stop(25%, black));
  background: url("./images/targaryen.png") center no-repeat, -webkit-linear-gradient(#A9181F 75%, black 25%);
  background: url("./images/targaryen.png") center no-repeat, -o-linear-gradient(#A9181F 75%, black 25%);
  background: url("./images/targaryen.png") center no-repeat, linear-gradient(#A9181F 75%, black 25%);
  background-size: contain;
}

.lannister-f {
  background: #C2AD78;
  border-top: solid #87090A 10px;
  border-left: solid #87090A 10px;
}

.lannister-b {
  background: url("./images/lannister.png") center no-repeat, -webkit-gradient(linear, left top, left bottom, color-stop(75%, #C2AD78), color-stop(25%, #87090A));
  background: url("./images/lannister.png") center no-repeat, -webkit-linear-gradient(#C2AD78 75%, #87090A 25%);
  background: url("./images/lannister.png") center no-repeat, -o-linear-gradient(#C2AD78 75%, #87090A 25%);
  background: url("./images/lannister.png") center no-repeat, linear-gradient(#C2AD78 75%, #87090A 25%);
  background-size: contain;
}



